.github-profile-container{
    margin: 0 auto;
    width: 90%;max-width: 1000%;
    padding: 20px;
    border-radius: 8px;

}

.input-wrapper{
    display: flex;
    gap: 20px;
    justify-content: center;
    margin-bottom: 20px;
}

.input-wrapper input{
    padding: 12px;
    font-size: 16px;
    border: none;
}

.input-wrapper button {
    padding: 5px 12px;
    border: none;
    border-radius: 8px;
    background-color: #09edde;
    color: #000;
    cursor: pointer;
}

.user{
    padding: 15px;
    border-radius: 10px;
    border: 1px bold;
}

.avatar{
    height: 150px;
    aspect-ratio: 1/1;
    border-radius: 50%;

}

.name-container{
    display: flex;
    font-size: 20px;
    justify-content: center;
    margin-top: 20px;
}
.name-container p{
    margin: 0px;
    font-size: 20px;
    font-weight: bold;
}
.profile-info div{
    display: flex;
    justify-content: center;
    gap: 20px;
    font-size: 20px;
    font-weight: bold;

}
